@font-face {
  font-family: "SF";
  src:url("../fonts/SF Font for Windows/SF-UI-Display-Black.ttf");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "SF";
  src:url("../fonts/SF Font for Windows/SF-UI-Display-Bold.ttf");
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: "SF";
  src:url("../fonts/SF Font for Windows/SF-UI-Text-Regular.ttf");
  font-weight: normal;
  font-style: normal;
}

body{
  width:100%;
  height: 100%;
  font-family: "SF";
}

.wrapper {
  margin: 100px auto;
  text-align: center;
  .title{
    p{
      font-size: 2.3em;
      text-transform: uppercase;
    }
  }
  form {
    position: relative;
    input{
      padding: 5px;
    }
    .btn_submit{
      display: inline-block;
      color: white;
      font-weight: 700;
      text-decoration: none;
      user-select: none;
      padding: .5em 2em;
      outline: none;
      border: 2px solid;
      border-radius: 1px;
      transition: 0.2s;
      &:hover{
        background: rgba(255,255,255,.2);
        color: #000;
      }
      &:active{
        background: #000;
      }
    }
  }
  .FAP{
    position: relative;
    &__city-name{
      h3{
        font-size: 4em;
      }
      p{
        font-size: 1.5em;
      }
    }
    &__temp{
      h3{
        font-size: 9.5em;
      }
    }
  }
}
